import React, { Component } from 'react';

import './App.css';



class Drink extends Component {

    constructor(props) {
      super(props);
      this.state = { 
          lang: "English",
          time1: {}, seconds1: 60 ,
           
          time2: {}, seconds2: 40,
          
          time3: {}, seconds3: 20 ,

          drinktext: "what drink would you like?",
          show: true,
          drinkb: <div><button onClick={this.startTimer1}>tea?</button><button onClick={this.startTimer2}>coffee?</button><button onClick={this.startTimer3}>cocoa?</button><button onClick={this.changeColor4}>none?</button></div>,
          timedisplay: <h1>------</h1>,
          attribution: 
          <div>Icons made by <a href="https://www.freepik.com" title="Freepik" style={{color:"white"}}>Freepik</a> from <a href="https://www.flaticon.com/" title="Flaticon" style={{color:"white"}}>www.flaticon.com</a></div>,
          lang_b: <button onClick={this.ChangeLang}>中文翻譯</button>,
      };
      this.timer1 = 0;
      this.startTimer1 = this.startTimer1.bind(this);
      this.countDown1 = this.countDown1.bind(this);
      
      this.timer2 = 0;
      this.startTimer2 = this.startTimer2.bind(this);
      this.countDown2 = this.countDown2.bind(this);

      this.timer3 = 0;
      this.startTimer3 = this.startTimer3.bind(this);
      this.countDown3 = this.countDown3.bind(this);
    }
  
    shouldComponentUpdate() {
              return true;
          }
  
    secondsToTime1 = (secs1) => {
      let divisor_for_seconds1 = secs1 % (60 * 60);
      let seconds1 = Math.ceil(divisor_for_seconds1);
  
      let obj1 = {
        "s": seconds1
      };
      return obj1;
    }
    secondsToTime2 = (secs2) => {
        let divisor_for_seconds2 = secs2 % (60 * 60);
        let seconds2 = Math.ceil(divisor_for_seconds2);
    
        let obj2 = {
          "s": seconds2
        };
        return obj2;
      }
      
      secondsToTime3 = (secs3) => {
        let divisor_for_seconds3 = secs3 % (60 * 60);
        let seconds3 = Math.ceil(divisor_for_seconds3);
    
        let obj3 = {
          "s": seconds3
        };
        return obj3;
      }

    componentDidMount = () => {
      let timeLeftVar1 = this.secondsToTime1(this.state.seconds1);
      this.setState({ time1: timeLeftVar1 });

      let timeLeftVar2 = this.secondsToTime2(this.state.seconds2);
      this.setState({ time2: timeLeftVar2 });

      let timeLeftVar3 = this.secondsToTime3(this.state.seconds3);
      this.setState({ time3: timeLeftVar3 });
    }
      countDown1 = () => {
        let seconds1 = this.state.seconds1 - 1;
        this.setState({
          time1: this.secondsToTime1(seconds1),
          seconds1: seconds1,
        });
        if (seconds1 == 0 || seconds1 == 60) {    
            this.setState({timedisplay: <h1 style={{color:"white"}}> s: {this.state.time1.s}</h1>})
        if(this.state.lang=="English"){
            this.setState({drinktext:"your tea is ready!"})
        }else{
            this.setState({drinktext: "您的綠茶準備好了!"})
        }
          clearInterval(this.timer1);
          this.ToggleClick();
          let seconds1 = 60;
          this.setState({
            time1: this.secondsToTime1(seconds1),
            seconds1: seconds1,
          });   
          this.setState({timedisplay: <h1 style={{color:"white"}}> s: {this.state.time1.s}</h1>})     
        }
       if(seconds1 < 60 && seconds1 != 0){
          this.setState({timedisplay: <h1 style={{color:"green"}}> s: {this.state.time1.s}</h1>})
          if(this.state.lang=="English"){
              this.setState({drinktext: "preparing tea..."})
          }else if(this.state.lang=="Chinese"){
              this.setState({drinktext:"現在做綠茶..."})
          }
      }
        if(seconds1 < 16 && seconds1 != 0){
            this.setState({ timedisplay: <h1 style={{color:"red"}}> s: {this.state.time1.s}</h1>})
          if(this.state.lang=="English"){
              this.setState({drinktext: "almost tea time..."})
          }else if(this.state.lang=="Chinese"){
              this.setState({drinktext:"綠茶時間快到了..."})
          }   
          }
          }
          startTimer1 = () => {
            this.ToggleClick();
            this.timer1 = setInterval(this.countDown1, 1000);
            if(this.state.lang=="English"){
              this.setState({drinktext: "preparing tea..."})
            }else if(this.state.lang=="Chinese"){
              this.setState({drinktext:"現在做綠茶..."})
          }
            
            this.setState({timedisplay: <h1> s: {this.state.time1.s}</h1>})
        }

    countDown2 = () => {
        let seconds2 = this.state.seconds2 - 1;
        this.setState({
          time2: this.secondsToTime2(seconds2),
          seconds2: seconds2,
        });
        if (seconds2 == 0 || seconds2 == 40) {    
        this.setState({timedisplay: <h1 style={{color:"white"}}> s: {this.state.time2.s}</h1>})
        if(this.state.lang=="English"){
            this.setState({drinktext:"your coffee is ready!"})
        }else{
            this.setState({drinktext: "您的咖啡準備好了!"})
        }
          clearInterval(this.timer2);              
          this.ToggleClick();
          let seconds2 = 40;
          this.setState({
            time2: this.secondsToTime2(seconds2),
            seconds2: seconds2,
          });      
        this.setState({timedisplay: <h1 style={{color:"white"}}> s: {this.state.time2.s}</h1>})
        }
        if(seconds2 < 40 && seconds2 != 0){
            this.setState({timedisplay: <h1 style={{color:"green"}}> s: {this.state.time2.s}</h1>})
            if(this.state.lang=="English"){
                this.setState({drinktext: "preparing coffee..."})
            }else if(this.state.lang=="Chinese"){
                this.setState({drinktext:"現在做咖啡..."})
            }
        }           
        if(seconds2 < 11 && seconds2 !=0){                
            this.setState({ timedisplay: <h1 style={{color:"red"}}> s: {this.state.time2.s}</h1>})
            if(this.state.lang=="English"){
                this.setState({drinktext: "almost coffee time..."})
            }else if(this.state.lang=="Chinese"){
                this.setState({drinktext:"咖啡時間快到了..."})
            }    
        }
      }
    startTimer2 = () => {
    this.ToggleClick();      
    this.timer2 = setInterval(this.countDown2, 1000);
    if(this.state.lang=="English"){
        this.setState({drinktext: "preparing coffee..."})
    }else if(this.state.lang=="Chinese"){
        this.setState({drinktext:"現在做咖啡..."})
    }
    this.setState({timedisplay: <h1 style={{color:"white"}}> s: {this.state.time2.s}</h1>})
    }
    //
          countDown3 = () => {
            let seconds3 = this.state.seconds3 - 1;
            this.setState({
              time3: this.secondsToTime3(seconds3),
              seconds3: seconds3,
            });
            if (seconds3 == 0 || seconds3 == 20) {   
                this.setState({timedisplay: <h1 style={{color:"white"}}> s: {this.state.time3.s}</h1>})
            if(this.state.lang=="English"){
                this.setState({drinktext:"your cocoa is ready!"})
            }else{
                this.setState({drinktext: "您的熱可可準備好了!"})
            }
              clearInterval(this.timer3);
              this.ToggleClick();             
            let seconds3 = 20;
            this.setState({
              time3: this.secondsToTime3(seconds3),
              seconds3: seconds3,
            }); 
            this.setState({timedisplay: <h1 style={{color:"white"}}> s: {this.state.time3.s}</h1>})
            }
            if(seconds3 < 20 && seconds3 != 0){
                this.setState({ timedisplay: <h1 style={{color:"green"}}> s: {this.state.time3.s}</h1>})
                if(this.state.lang=="English"){
                    this.setState({drinktext: "preparing cocoa..."})
                }else if(this.state.lang=="Chinese"){
                    this.setState({drinktext:"現在做熱可可..."})
                }
            }
            if(seconds3 < 6 && seconds3 != 0){
                this.setState({ timedisplay: <h1 style={{color:"red"}}> s: {this.state.time3.s}</h1>})
                if(this.state.lang=="English"){
                    this.setState({drinktext: "almost cocoa time..."})
                }else if(this.state.lang=="Chinese"){
                    this.setState({drinktext:"熱可可時間快到了..."})
                }
            }
          }
    startTimer3 = () => {
        this.ToggleClick();
        this.timer3 = setInterval(this.countDown3, 1000);    
        if(this.state.lang=="English"){
            this.setState({drinktext: "preparing cocoa..."})
        }else if(this.state.lang=="Chinese"){
            this.setState({drinktext:"現在做熱可可..."})
        }
        this.setState({timedisplay: <h1 style={{color:"white"}}> s: {this.state.time3.s}</h1>})
      }
    changeColor4 = () => {       
        if(this.state.lang=="English"){
            this.setState({drinktext: "okay, I'll just read a book."})
        }else if(this.state.lang=="Chinese"){
            this.setState({drinktext:"好的，我要閱讀一本書."})
        }
        //this.ToggleClick();       
    }
    ToggleClick = () => {
        this.setState({ show: !this.state.show });
      }  
      
      ChangeLang = () => { 
          if(this.state.lang=="English"){
            this.setState({lang: "Chinese"})
            this.setState({drinktext: "現在做中文翻譯..."});
            this.setState({drinkb: <div><button onClick={this.startTimer1}>綠茶?</button><button onClick={this.startTimer2}>咖啡?</button><button onClick={this.startTimer3}>熱可可?</button><button onClick={this.changeColor4}>沒有?</button></div>})
            this.setState({attribution: <div>圖像創造者是 <a href="https://www.freepik.com" title="Freepik" style={{color:"white"}}>Freepik</a> 從 <a href="https://www.flaticon.com/" title="Flaticon" style={{color:"white"}}>www.flaticon.com</a></div>})
            this.setState({lang_b: <button onClick={this.ChangeLang}>English Translation</button>})
          }else if(this.state.lang=="Chinese"){
            this.setState({lang: "English"})  
            this.setState({drinktext: "creating English translation..."})
            this.setState({drinkb: <div><button onClick={this.startTimer1}>tea?</button><button onClick={this.startTimer2}>coffee?</button><button onClick={this.startTimer3}>cocoa?</button><button onClick={this.changeColor4}>none?</button></div>})
            this.setState({attribution: <div>Icons made by <a href="https://www.freepik.com" title="Freepik" style={{color:"white"}}>Freepik</a> from <a href="https://www.flaticon.com/" title="Flaticon" style={{color:"white"}}>www.flaticon.com</a></div>})
            this.setState({lang_b: <button onClick={this.ChangeLang}>中文翻譯</button>})
            }
      }   
    render = () => {
      return(
        <div>
        { this.state.show ? <h1>{ this.state.clicks }</h1> : '' }
        <h1>{this.state.drinktext}</h1>
        {this.state.timedisplay}
        
            { this.state.show ? <h1>{ this.state.drinkb }</h1> : '' }
        {this.state.lang_b}
            <br></br>
            <br></br>
        {this.state.attribution}
        </div>
      );
    }
}

export default Drink;