import React, { Component } from 'react';
import './App.css';

class Nav extends Component {

    constructor(props){
        super(props);
        this.state = {
          rotation: 0, 
          clicks: 0,
          show: true
        }
        
      }
      rotate_left = () => {
          this.setState({rotation: this.state.rotation + 15*3});
      }
      rotate_right = () => {
        this.setState({ rotation: this.state.rotation - 15*3, });
      }
      IncrementItem = () => {
        this.setState({ clicks: this.state.clicks + 1 });
      }
      DecreaseItem = () => {
        this.setState({ clicks: this.state.clicks - 1 });
      }
      ToggleClick = () => {
        this.setState({ show: !this.state.show });
      }

      render(){
        const { rotation } =  this.state;
        return <div><img style={{transform: `rotate(${rotation}deg)`}} src={"https://image.flaticon.com/icons/png/512/4760/4760414.png"} onClick={this.rotate_right} height="300" className="compass" /><br></br><button onClick={this.rotate_left}><img src="https://image.flaticon.com/icons/png/512/5553/5553194.png" height="25"></img></button><button onClick={this.rotate_right}><img src="https://image.flaticon.com/icons/png/512/5553/5553521.png" height="25"></img></button><br></br>
          <button onClick={this.IncrementItem}><img src="https://image.flaticon.com/icons/png/512/5553/5553708.png" height="25"></img></button>
          <button onClick={this.DecreaseItem}><img src="https://image.flaticon.com/icons/png/512/5553/5553210.png" height="25"></img></button>
          { this.state.show ? <h2>{ this.state.clicks }</h2> : '' }
        
        </div>
        
      }
}
export default Nav;